import React, { Component } from "react";
import './FullMeal.css';
 
class FullMeal extends Component {
  constructor(props) {
    super(props);
    this.state = {
      objMeal: [],
      ingredients: [],
      mealImage: [],
      measurments: [],
    };
  }

  componentDidMount() {     
    let currentLocation = window.location.href;
    let url = new URL(currentLocation);
    let searchParams = url.searchParams.get("id")
    
    
    fetch(`https://www.themealdb.com/api/json/v1/1/lookup.php?i=${searchParams}`)
      .then((response) => {
        return response.json();
      })
      .then((data) => {
        this.setState({ objMeal: data.meals });
        for (let i = 0; i <= 20; i++) {
          if (this.state.objMeal[0]["strIngredient" + i] && this.state.objMeal[0]["strMeasure" + i]) {
            this.setState({
              ingredients: [
                ...this.state.ingredients,
                this.state.objMeal[0]["strMeasure" + i] + " / " + this.state.objMeal[0]["strIngredient" + i]
              ],
            });
          }
        }
        this.setState({ mealImage: this.state.objMeal[0].strMealThumb });
      });
  }


  render() {
    return (
      <div className="fullmeal-div">
        <div className="flex-l big-div flex-wrap basketville">
          <div className="fullMeal-image w-50-l ma3-l" style={{backgroundImage: `url(${this.state.mealImage})`}}>
            {this.state.objMeal.map((meal, i) => {
          return (
            <h1 className="fullmeal-name w-50 pa3 normal tracked" key={i}>
              {meal.strMeal}
            </h1>
          );
        })}
          </div>
          <div className="w-30-l ma1">
            <h3 className="tl ">Ingredients:</h3>
            <hr />
            <div className="tl ttc f4">
              {this.state.ingredients.map((ingredient, i) => {
                return (
                  <p className="ma2 " key={i}>
                    <input type="checkbox" /> {ingredient}
                  </p>
                );
              })}
            </div>
          </div>
          <div className="ma3">
            <h3 className="tl f3">Instructions:</h3>
            <hr />
            {this.state.objMeal.map((meal, i) => {
              return (
                <p className="tl f4" key={i}>
                  {meal.strInstructions}
                </p>
              );
            })}
          </div>
        </div>
      </div>
    );
  }
}

export default FullMeal;
