import React from "react";
import './Meal.css';

const Meal = ({image, title, category}) => {
  
    return (
      <div className="card baskerville">
        <div className="meal-content" style={{backgroundImage: `url(${image})`}}>
          <div className="text-content">
          <div className="text-border">
          <h1 className="f4 tl normal">{title}</h1>
          <p className="tl f5">
            Category: {category}
          </p>
          </div>
          </div>
        </div>
      </div>
    );
  }

export default Meal;
