import React from 'react';
import './Searchfield.css';
import logo from './logo.jpg'

const Searchfield = ({searchChange}) => {
    return (
        <div className="header">
            <input type="text" 
            onChange={searchChange} 
            className="f3 w-50 w-40-l" 
            placeholder="Search..."/>
            <div className="w-50 flex justify-center near-white">
            Be-A-Foodie  <img src={logo} className="br-100"alt="logo" />  Food Recipes
            </div>
        </div>
    )
}


export default Searchfield;