import React from 'react';
import './App.css';
import MealList from './components/MealList';
import Searchfield from './components/Searchfield';
import Scroll from './components/Scroll';
import FullMeal from './components/FullMeal';
import "tachyons";
import { Route, Switch } from 'react-router-dom';

export default class App extends React.Component{
  constructor(){
    super();
    this.state = {
      recipes: [],
      searchInput: '',
    }
  }

  componentDidMount(){
    fetch(`https://www.themealdb.com/api/json/v1/1/search.php?s=${this.state.searchInput}`).then(response =>{
      return response.json()
    }).then(data =>{
      this.setState({recipes: data.meals})
    })
  }

  onInputChange = (event) => {
    this.setState({searchInput: event.target.value})
  }


  render(){
    const filteredRecipe  = this.state.recipes.filter(recipe => {
      return recipe.strMeal.toLowerCase().includes(this.state.searchInput.toLowerCase())
    })
    if(this.state.recipes.length === 0){
      return <h1>Ooops, no recipes at this time!</h1>
    }
    return(
      <div className="tc">
        <Switch>
        <Route exact path="/fullMeal">
        <FullMeal />
        </Route>
        <Route excat path="/">
        <Searchfield searchChange={this.onInputChange}/>
        <Scroll>
        <div className="flex flex-wrap justify-center">
          <MealList recipes={filteredRecipe}/>
        </div>
        </Scroll>
        </Route>
      </Switch>
      </div>
    )
  }
}
